import React from "react";

import TempCard from './TempCard/TempCard'
import SearchBar from './SearchBar/SearchBar'
import './SearchTemp.css'

export default function SearchTemp({onSearch, ciudad,tarjet,cerrar}) {
    

    return(
    <div>
        { tarjet ? <TempCard ciudad={ciudad} cerrar={cerrar} /> : null }

        <SearchBar onSearch={onSearch} />
    </div>
    )}