import React, { useEffect, useState } from 'react';
import './App.css';
import SearchTemp from './components/SearchTemp/SearchTemp'
import CapitalsTemp from './components/CapitalsTemp/CapitalsTemp';

export default function App() {

  const [cities,setCities] = useState('');
  const apiKey = '87dbd105850f2d4ef56edebd9d913f8a';
  const [tarjet, setTarjet] = useState(false)

  const [capitales,setCapitales] =  useState({})

  var copia = {
    'Rio de Janeiro':null,
    'Sao paulo':null,
    'Belo Horizonte':null,
    'Brasilia':null,
    'Belen':null,
    'Salvador':null,
    'Curitiba':null,
    'Fortaleza':null,
    'Manaus':null,
    'Joao Pessoa':null
  }

 useEffect(  () =>{  async function data(){

  
   for (var s in copia){ 
    var valores = await fetch(`https://api.openweathermap.org/data/2.5/weather?q=${s}&appid=${apiKey}&units=metric&lang=pt_br`)
    valores = await valores.json()
    var agregar = {'min': Math.round(valores.main.temp_min), 'max':Math.round(valores.main.temp_max)}
    copia[s] = agregar
   }
   
   document.getElementById("loading").style.display = 'none';   
   document.getElementById("minMax1").style.display = 'grid';   
   document.getElementById("minMax2").style.display = 'grid';   

  setCapitales(copia)
 } data()},[]

 )

   

  function cerrar(){
    setTarjet(false)
  }

   function onSearch(ciudad) {
    fetch(`https://api.openweathermap.org/data/2.5/weather?q=${ciudad}&appid=${apiKey}&units=metric&lang=pt_br`)
    .then(r => r.json())
    .then((recurso) => {
      if(recurso.main !== undefined){
   
          const ciudad = {
            min: Math.round(recurso.main.temp_min),
            max: Math.round(recurso.main.temp_max),
            img: recurso.weather[0].icon,
            id: recurso.id,
            wind: recurso.wind.speed,
            temp: Math.round( recurso.main.temp),
            name: recurso.name + ' - ' +recurso.sys.country,
            weather: recurso.weather[0].description,
            clouds: recurso.clouds.all,
            latitud: recurso.coord.lat,
            longitud: recurso.coord.lon
          }


          setCities(ciudad)
          setTarjet(true)
      
          document.getElementById("titulo").style.top = 0;
          document.getElementById("searchBar").style.top = 0;      


        } else {
          alert("Ciudad no encontrada");
        }
      });

    }
   
  return (
    <div className="App">
      
      <h1 id='titulo' style={{position:'relative',top:-60,fontSize:50,marginBottom:10,marginTop:'30%'}}>Previsão do tempo</h1>
      <SearchTemp onSearch={onSearch} ciudad={cities} tarjet={tarjet} cerrar={cerrar} />
      <hr style={{width: '120%',color:'none',borderBottom:'1px solid white',borderTop:'none',margin:0}}/>
      <CapitalsTemp capitales={capitales}/>
     

    </div>
  );
}

