import React from "react";
import './CapitalsTemp.css'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome' 
 import {faSpinner} from '@fortawesome/free-solid-svg-icons' 

export default function CapitalsTemp({capitales}) {
    
   capitales = (Object.keys(capitales).map((key) => [(key), capitales[key]]))

   // valores de columnas
   var primeraMitad=capitales.slice(0,5) // primera mitad
   var segundaMitad = capitales.slice(5,10) // segunda mitad

    return(
    <div style={{width:'100%',height:'100%',display:'flex',flexDirection:'column',marginLeft:50,marginBottom:30}}>

    <div style={{gap:1,width:'100%',height:'100%',}}>
                <div style={{width:'100%',display:'flex',alignSelf:'left'}}>
                    <h1 >Capitais</h1>
                    
                </div>
                
                <div style={{display:'grid', gridTemplateColumns: '1fr 1fr', position:'relative',gridTemplateRows: '0.25fr repeat(2, minmax(0, 0.7fr))',}}>
{/* ///////////https://keithclark.co.uk/articles/targeting-first-and-last-rows-in-css-grid-layouts////////////////////// */}
            <FontAwesomeIcon style={{position:'absolute', left:'30vh',top:50}} size='2x' id='loading' className="loader" icon={faSpinner}/>
                <div className='minMax' id='minMax1' style={{fontSize: 17, gridColumn:1,gridRow:1, color:'black' ,size:'100%',gap:'0 3%',gridTemplateColumns:'30px 30px auto',marginRight:50}}>
                    <div style={{fontWeight:'300',gridColumn:2,gridRow:1,display: 'flex',fontSize:13}}>Máx</div>
                    <div style={{fontWeight:'300',gridColumn:1,gridRow:1,display: 'flex',fontSize:13}}>Min</div>
                </div>    
                <div style={{fontSize: 17, color:'black' ,size:'100%',gap:'20% 4%',gridRow: 2,gridColumn: 1, display: 'grid',gridTemplateColumns:'30px 30px auto',marginRight:30}}>
                     {primeraMitad.map(p =><> 
                        <div style={{gridColumn:1,display: 'flex',alignItems:'flex-start', alignSelf: 'flex-start'}}>
                        <strong>{p[1]['min']}°</strong>
                        </div>
                        <div style={{gridColumn:2,display: 'flex',alignItems:'flex-start'}}>
                            <strong>{p[1]['max']}°</strong>
                        </div>
                        <div style={{gridColumn:3,display: 'flex',alignItems:'flex-start'}}>
                            <strong>{p[0]}</strong>
                        </div>
                        
                        </>
                        )} 
                    </div>


                        <div className='minMax' id='minMax2' style={{fontSize: 17, color:'black' ,size:'100%',gap:'20% 3%',gridTemplateColumns:'30px 30px auto',marginRight:50}}>
                            <div style={{fontWeight:'300',gridColumn:1,gridRow:1,display: 'flex',fontSize:13}}>Min</div>
                            <div style={{fontWeight:'300',gridColumn:2,gridRow:1,display: 'flex',fontSize:13}}>Máx</div>
                        </div>
                    
                        <div style={{fontSize: 17, color:'black' ,size:'100%',gap:'20% 4%',gridRow: 2,gridColumn: 2, display: 'grid',gridTemplateColumns:'30px 30px auto',marginRight:30}}>
                        
                        {segundaMitad.map(p =><> 
                        <div style={{gridColumn:1,display: 'flex',alignItems:'flex-start', alignSelf: 'flex-start'}}>
                        <strong>{p[1]['min']}°</strong>
                        </div>
                        <div style={{gridColumn:2,display: 'flex',alignItems:'flex-start'}}>
                            <strong>{p[1]['max']}°</strong>
                        </div>
                        <div style={{gridColumn:3,display: 'flex',alignItems:'flex-start'}}>
                            <strong>{p[0]}</strong>
                        </div>
                        
                        </>
                        )}  
                            
                            
                        </div>
                        
                     </div>

                </div>
               
    </div>
    )}