import React from "react";
import './tempCard.css'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome' 
import {faArrowUp} from '@fortawesome/free-solid-svg-icons' 
import {faArrowDown} from '@fortawesome/free-solid-svg-icons' 

 
export default function SearchTemp({ciudad,cerrar}) {
  //  console.log(ciudad.name)
    return(
    <div>

        <div style={{position:'relative',backgroundColor: 'white',width:400,height: 'auto',border: '2px solid #ff7f00',color:'#505050',marginTop:0}}>
        <button style={{position:'absolute',right:'10px',top:'10px',border:'none',color:'#ff7f00',backgroundColor:'white',fontSize:20}} onClick={function () {
        document.getElementById("titulo").style.top = '-60px'; 
        document.getElementById("searchBar").style.top = '-30px'; 
        cerrar() 
        }}> <strong>x</strong> </button>
                <div style={{margin: '25px 40px 5px 30px',display:'flex',flexDirection: 'column',alignItems:'flex-start',fontSize: 14}}>
                    <div style={{fontWeight: 700}}>{ciudad.name}</div>

                    <div>
                        <h1 style={{margin:'10px',marginLeft:0,fontSize: '45px',color:'#505050'}}>{ciudad.temp}°C {ciudad.weather}</h1>
                    </div>

                    <div style={{display:'grid',gridTemplateColumns: '1fr 1fr 2fr' , gap:'15px 10px',marginBottom:'15px'}}>
                        <div style={{gridColumn:1,display: 'block',textAlign: 'left',marginLeft:10}}>
                        <FontAwesomeIcon style={{color:'orange'}} icon={faArrowUp} /> <strong>{ciudad.min}°</strong>

                        </div>
                        <div style={{gridColumn:2,display: 'block',textAlign: 'left'}}>
                        <FontAwesomeIcon style={{color:'orange'}} icon={faArrowDown} /> <strong>{ciudad.max}°</strong>

                        </div>
                        <div style={{gridColumn:'1/3',display: 'block',textAlign: 'left'}}>
                            <div style={{"fontWeight":"300"}}>Viento: <strong>{ciudad.wind} Km/h</strong>
                            </div>
                        </div>
                        <div style={{gridColumn:'3/4',gridRow:1,display: 'block',textAlign: 'left'}}>
                            <div style={{"fontWeight":"300"}}>Sensação: <strong>19°C</strong>
                            </div>
                        </div>
                        <div style={{gridColumn:'3/4',display: 'block',textAlign: 'left'}}>
                            <div style={{"fontWeight":"300"}}>Humidade: <strong>89%</strong>
                            </div>
                        </div>
                    </div>
                    <hr style={{width:'100%',borderTop:'1px solid orange'}}/>
                    <div style={{width:'70%',marginLeft:10,marginTop:5,display: 'grid',gridTemplateColumns: '1fr 1fr 1fr 1fr 1fr ',gap:'10px 25px'}}>
                        <div style={{fontWeight:700,gridColumn: 1,gridRow:1,display:'block',textAlign: 'left'}}>Terça</div>
                        <div style={{fontWeight:700,gridColumn:1,gridRow:2,display:'block',textAlign: 'left',color:'#ff7f00'}}>18°26°</div>
                        <div style={{fontWeight:700,gridColumn:2,gridRow:1,display:'block',textAlign: 'left'}}>Cuarta</div>
                        <div style={{fontWeight:700,gridColumn:2,gridRow:2,display:'block',textAlign: 'left',color:'#ff7f00'}}>18°28°</div>
                        <div style={{fontWeight:700,gridColumn:3,gridRow:1,display:'block',textAlign: 'left'}}>Quinta</div>
                        <div style={{fontWeight:700,gridColumn:3,gridRow:2,display:'block',textAlign: 'left',color:'#ff7f00'}}>19°30°</div>
                        <div style={{fontWeight:700,gridColumn:4,gridRow:1,display:'block',textAlign: 'left'}}>Sexta</div>
                        <div style={{fontWeight:700,gridColumn:4,gridRow:2,display:'block',textAlign: 'left',color:'#ff7f00'}}>23°35°</div>
                        <div style={{fontWeight:700,gridColumn:5,gridRow:1,display:'block',textAlign: 'left'}}>Sabado</div>
                        <div style={{fontWeight:700,gridColumn:5,gridRow:2,display:'block',textAlign: 'left',color:'#ff7f00'}}>23°37°</div>
                    </div>
                </div>
            </div>

    </div>
    )

}