import React, {useState} from "react";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome' 
 import {faSearch} from '@fortawesome/free-solid-svg-icons' 
 import './SearchBar.css'


export default function SearchTemp({onSearch, tarjet}) {
    const [city, setCity] = useState('');

    return (
        <div id='searchBar' style={{position:'relative',top:-30}}>

            <div style={
                {
                    position: 'relative',
                    width: '100%'
                }
            }>
                <form style={{display:'flex',alignItems:'center'}} onChange= { (e) => setCity(e.target.value) }
                    onSubmit={
                        (e) => {
                            e.preventDefault();
                            // console.log(city);
                            onSearch(city)
                        }
                }>
                    <input placeholder=" Inserta nombre de la ciudade"
                        style={
                            {
                                fontSize: 20,
                                marginBottom: '3%',
                                marginTop: '3%',
                                height: '50px',
                                width: 390,
                                border: '2px solid #ff7f00',
                                borderRadius: 0,
                                paddingLeft:10
                            }
                    }></input>               
   <button type="submit" style={{height:'50%',border:'none',position:'absolute',right:10}}><FontAwesomeIcon icon={faSearch} /> </button>
                </form>
                            
            </div>


        </div>
    )
}
